/*
 * 	FILTER
 */

.filter{
	padding: 40px 0 40px 20px;
	list-style: none;
	overflow: hidden;
	display: none;

	@media (min-width: @screen-sm-min) {
		display: block;
	}

	li{
		position: relative;
		float: left;
		margin-right: 35px;
		font-size: 24px;
		cursor: pointer;

		&.selected{
			font-family: @bold;
		}
	}
}