/*
 * 	LOADER
 */

.loader{
	position: fixed;
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
	visibility: hidden;
	opacity: 0;
	transition: all .5s ease;
	transform: scale(1.1); 
	background-color: rgba(255, 255, 255, .75);

	.loading_project &{
		visibility: visible;
		opacity: 1;
		transform: scale(1);	
	}
}