/*
 * 	HEAD
 */

.head{
	width: 100%;
	height: 114px;

	@media (min-width: @screen-sm-min) {
		// height: 88px;
		padding: 0 15px;
	}

	.links{
		height: 100%;
		width: 100%;
		right: 0;
		overflow: hidden;

		a{
			display: table;
			height: 100%;
			width: 20%;
			position: relative;
			float: right;
			background-size: auto 30px;
			background-position: center center;
			background-repeat: no-repeat;

			@media (min-width: @screen-sm-min) {
				background-position: center 45px;
				width: 93px;
			}

			&.email{     background-image: url(../img/nav/email.png);     }
			&.instagram{ background-image: url(../img/nav/instagram.png); }
			&.linkedin{  background-image: url(../img/nav/linkedin.png);  }
			&.medium{    background-image: url(../img/nav/medium.png);    }
			&.twitter{   background-image: url(../img/nav/twitter.png);   }

			img{
				height: 30px;
				margin: 0 auto;
				display: table-cell;
				vertical-align: middle;
			}
		}
	}
}