/*
 * 	GRID
 */

.grid{
	width: 100%;

	.tile{
		width: 100%;
		cursor: pointer;
		// padding: 0 15px 40px;
		// margin: 0 15px 40px;
		// margin: 0 0 40px;


		@media (min-width: @screen-sm-min) {
			width: 50%;
		}

		@media (min-width: @screen-md-min) {
			width: 33.33333%;
		}

		@media (min-width: @screen-lg-min) {
			width: 25%;
		}

		.tile_interior{
			padding: 0 15px 40px;

			img{
				width: 100%;
				display: block;
				margin-bottom: 10px;
			}

			p{
				margin: 0;

				b{
					font-family: @bold;
				}
			}
		}
	}
}