/*
 * 	TYPOGRAPHIE
 */

// Headings
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6{
	font-family: inherit;
	font-weight: 500;
	line-height: 1.1;
	color: inherit;

	margin-top: 20px;
	margin-bottom: 10px;
}

h1,
.h1 {
  font-size: 36px;
}
h2,
.h2 {
  font-size: 30px;
}

/*
 * 	ETC
 */

p {
  margin: 0 0 10px; 
}

a{
	text-decoration: none;
}

ul, ol {
	margin-top: 0;
	margin-bottom: 10px;
}