/*
 * 	PROJECT
 */

.project{
	position: fixed;
	height: 100vh;
	width: 100%;
	top: 0;
	left: 0;
	visibility: hidden;
	opacity: 0;
	overflow: scroll;
	transform: scale(1.1); 
	background: rgba(255,255,255,0.75);
	background: -moz-linear-gradient(top, rgba(255,255,255,0.75) 0%, rgba(255,255,255,1) 75%, rgba(255,255,255,1) 100%);
	background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(255,255,255,0.75)), color-stop(75%, rgba(255,255,255,1)), color-stop(100%, rgba(255,255,255,1)));
	background: -webkit-linear-gradient(top, rgba(255,255,255,0.75) 0%, rgba(255,255,255,1) 75%, rgba(255,255,255,1) 100%);
	background: -o-linear-gradient(top, rgba(255,255,255,0.75) 0%, rgba(255,255,255,1) 75%, rgba(255,255,255,1) 100%);
	background: -ms-linear-gradient(top, rgba(255,255,255,0.75) 0%, rgba(255,255,255,1) 75%, rgba(255,255,255,1) 100%);
	background: linear-gradient(to bottom, rgba(255,255,255,0.75) 0%, rgba(255,255,255,1) 75%, rgba(255,255,255,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#ffffff', GradientType=0 );

	transition: all .5s ease;
	-webkit-backface-visibility: hidden;
	display: table;

	@media (min-width: @screen-sm-min) {
		display: block;
	}

	.showing_project &{
		visibility: visible;
		opacity: 1;
		transform: scale(1);	
	}

	.project_close_exterior{
		position: absolute;
		width: 100%;

		@media (min-width: @screen-sm-min) {
			position: relative;
		}

		.project_close_interior{
			position: relative;
			max-width: 1140px;
			width: 100%;
			margin: 0 auto;

			.project_close_button{
				position: absolute;
				top: 20px;
				right: 0;
				height: 60px;
				width: 60px;
				cursor: pointer;
				z-index: 2;
				.icon(0, 0, 60px);

				@media (min-width: @screen-sm-min) {
					top: 10px;
					right: -3px;
				}

				@media (min-width: @screen-lg-min) {
					// top: 20px;
					right: -25px;
				}
			}
		}
	}
}

.project_exterior{
	position: relative;
	height: 100%;
	width: 100%;
	top: 0;
	left: 0;
	display: table-cell;
	vertical-align: middle;
	z-index: 1;

	@media (min-width: @screen-sm-min) {
		position: absolute;
		display: block;
	}
}

.project_interior{
	position: relative;
	max-width: 1140px;
	width: 100%;
	margin: 0 auto;
	height: auto;
	display: table-cell;
	padding: 15px;

	@media (min-width: @screen-sm-min) {
		padding: 40px 15px;
		display: block;
	}

	@media (min-width: @screen-lg-min) {
		padding: 40px 0;
	}

	.tall&{
		@media (min-width: @screen-sm-min) {
			padding: 40px 0 0;
		}
	}

	.project_gallery{
		height: 100%;
		display: none;
		margin: 0;
		padding: 40px 40px 80px;
		background-color: white;

		@media (min-width: @screen-sm-min) {
			display: block;
			// transition: all .5s ease;
		}

		.tall&{
			width: 50%;
			margin: 0 auto 80px;

			@media (min-width: @screen-sm-min) {
				width: 50%;
				margin: 0 15px;
			}

			@media (min-width: @screen-md-min) {
				width: 66.6666%;
			}

			@media (min-width: @screen-lg-min) {
				width: 50%;
				margin: 0 auto;		
			}
		}

		&.single{
			padding-bottom: 40px;
		}

		.slick-arrow{
			height: 60px;
			width: 60px;
			position: absolute;
			top: 50%;
			margin-top: -45px;
			display: block;
			z-index: 1;
			cursor: pointer;

			&.prev{
				.icon(0, 1, 60px);

				@media (min-width: @screen-sm-min) {
					left: -17px;
				}
				@media (min-width: @screen-lg-min) {
					left: -25px;
				}
			}
			&.next{
				.icon(0, 2, 60px);
				@media (min-width: @screen-sm-min) {
					right: -17px;
				}
				@media (min-width: @screen-lg-min) {
					right: -25px;
				}
			}
		}

		.slick-dots {
		    position: absolute;
		    left: 0;
		    bottom: 30px;
		    list-style: none;
		    display: block;
		    text-align: center;
		    padding: 0;
		    margin: 0;

		    @media (min-width: @screen-sm-min) {
		    	width: 100%;
		    }
		    @media (min-width: @screen-md-min) {
			    width: 66.6666;
		    }
		    @media (min-width: @screen-lg-min) {
			    width: 100%;
		    }

		    li {
		        position: relative;
		        display: inline-block;
		        height: 20px;
		        width: 20px;
		        margin: 0 5px;
		        padding: 0;
		        cursor: pointer;

		        button {
		            border: 0;
		            background: transparent;
		            display: block;
		            height: 20px;
		            width: 20px;
		            outline: none;
		            line-height: 0px;
		            font-size: 0px;
		            color: transparent;
		            padding: 5px;
		            cursor: pointer;

		            &:hover, &:focus {
		                outline: none;
		                &:before {
		                    opacity: 1;
		                }
		            }

		            &:before {
		            	content: '';
		                position: absolute;
		                top: 50%;
		                left: 50%;
		                width: 6px;
		                height: 6px;
		                margin: -3px 0 0 -3px;
		                background-color: #444;
		                opacity: .4;
		                border-radius: 50%;
		            }
		        }
		        &.slick-active button:before {
		            opacity: 1;
		        }
		    }
		}

		.project_image{

			@media (min-width: @screen-sm-min) {
				// transition: all .5s ease;
			}
			
			.tall&{

				// @media (min-width: @screen-md-min) {
				// 	width: 50%;
				// }
			}

			img{
				width: 100%;
				height: auto;

				.tall&{
					margin: 0 auto;
					width: auto;
					height: 100%;
					// max-height: ~'calc(100vh - 80px)';
					max-height: 80vh;
				}
			}

			.slick-loading + .project_image_loader{
				display: block;
			}

			.project_image_loader{
				display: none;

				&:before{
					content: '';
					padding-bottom: 75%;
					display: block;
				}
			}
		}
	}

	.project_info{
		max-width: 550px;
		width: 100%;
		margin: 0 auto;

		@media (min-width: @screen-sm-min) {
			margin-bottom: 40px;
			// transition: all .5s ease;
		}

		.tall&{

			@media (min-width: @screen-sm-min) {
				position: absolute;
				padding: 80px 15px 0;
				bottom: 80px;
				right: 0;
				width: 50%;
				margin-bottom: 0;
			}

			@media (min-width: @screen-md-min) {
				width: 33.33333%;
			}

			@media (min-width: @screen-lg-min) {
				padding: 80px 20px 0;
				width: 25%;
			}
		}

		p:last-of-type{
			.tall&{
				margin-bottom: -6px;
			}
		}
	}
}
