/*
 * 	SKELETON
 */

* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

html,
body{
	position: relative;
	color: @color-copy;
	font-family: @standard;
	font-size: @base-font-size;
	line-height: @base-line-height;	
	height: 100%;
}

html{
	font-size: 10px;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body{
	margin: 0;
	&.showing_project{
		overflow: hidden;
	}
}

.main{
	width: 100%;
	margin: 0 auto;
	min-height: 100%;
}

.bio_exterior{
	background-color: transparent;

	.bio_interior{
		margin: 0 auto 30px;
		padding: 0 15px 30px;
		width: 100%;
		
		@media (min-width: 768px){	width: 750px;	}
		@media (min-width: 992px){	width: 970px;	}
		@media (min-width: 1200px){	width: 1170px;	}

		h1:first-of-type{
			margin: 0;
		}

		.work_link{
			color: @color-copy;
			transition: color .5s ease;

			&:hover,
			&:active{
				color: #000;
				text-decoration: none;
			}
		}
	}

	h1{
		margin-top: 20px;
		margin-bottom: 10px;
	}

	#show_work{
		cursor: pointer;
	}
}

.work_exterior{

	.work_interior{
		max-width: 1170px;
		width: 100%;
		margin: 0 auto;

		@media (min-width: @screen-sm-min) {
			padding: 0 15px;
		}

		@media (min-width: @screen-lg-min) {
			padding: 0;
		}
	}
}

b{
	font-weight: normal;
	font-family: @bold;
}




















